}

@font-face {
  font-family: DM Sans;
  font-style: italic;
  font-weight: normal;
  src: local(DM Sans Italic), url("DMSans-Italic.e947e2a0.woff") format("woff");
}

@font-face {
  font-family: DM Sans;
  font-style: normal;
  font-weight: 500;
  src: local(DM Sans Medium), url("DMSans-Medium.ea7c3d0c.woff") format("woff");
}

@font-face {
  font-family: DM Sans;
  font-style: italic;
  font-weight: 500;
