body {
  overflow: overlay;
  background-color: #f6f6f6;
  margin: 0;
  padding: 0;
  font-family: DM Sans;
}

.container {
  max-width: 375px;
  box-sizing: border-box;
  margin: 0 auto;
  padding: 0 20px;
}

@media screen and (min-width: 768px) {
  .container {
    max-width: 768px;
    padding: 0 32px;
  }
}

@media screen and (min-width: 1440px) {
  .container {
    max-width: 1440px;
    padding: 0 24px;
  }
}

.main-container {
  padding-top: 110px;
}

@media screen and (min-width: 1440px) {
  .main-container {
    gap: 40px;
    display: flex;
  }
}

@media screen and (max-width: 767px) {
  .section-books-cards {
    margin-top: 40px;
  }
}

@media screen and (min-width: 768px) and (max-width: 1439px) {
  .section-books-cards {
    margin-top: 100px;
  }
}

a {
  color: inherit;
  text-decoration: none;
}

h1, h2, h3, h4, h5, h6, p {
  margin: 0;
  padding: 0;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

.backdrop {
  z-index: 2;
  width: 100%;
  height: 100%;
  opacity: 1;
  background-color: rgba(0, 0, 0, .2);
  transition: opacity .25s cubic-bezier(.4, 0, .2, 1), visibility .25s cubic-bezier(.4, 0, .2, 1);
  position: fixed;
  top: 0;
  left: 0;
  overflow-y: scroll;
}

.backdrop--hidden {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
}

.modal {
  width: 100%;
  max-width: 335px;
  max-height: 762px;
  background-color: #fff;
  border: 2px solid #111;
  border-radius: 10px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

@media screen and (min-width: 768px) {
  .modal {
    max-width: 579px;
    max-height: 465px;
    top: 50%;
    transform: translate(-50%, -50%);
  }
}

.close-btn {
  position: absolute;
  top: 4px;
  right: 13px;
}

.close-icon {
  fill: #111;
}

.modal__btn {
  min-width: 211px;
  max-height: 46px;
  text-transform: uppercase;
