#toggle {
  -webkit-appearance: none;
  -ms-appearance: none;
  appearance: none;
  height: 20px;
  width: 40px;
  cursor: pointer;
  background: linear-gradient(#4f2ee8 0%, #dcdcdc 100%);
  border-radius: 20px;
  outline: none;
  position: relative;
  box-shadow: inset 1px 1px 2px rgba(0, 0, 0, .1);
}

@media (min-width: 1440px) {
  #toggle {
    margin-left: 745px;
    margin-right: 24px;
  }
}

#toggle:after {
  content: "";
  height: 18px;
  width: 18px;
  background: linear-gradient(#fff 0%, #f7f7f7 100%);
  border-radius: 50%;
  position: absolute;
  top: 1px;
  left: 1px;
  box-shadow: 1px 1px 2px rgba(0, 0, 0, .1), 0 2px 2px rgba(0, 0, 0, .1), 1px 2px 4px rgba(0, 0, 0, .1);
}

.dark-theme {
  background-color: #202024;
}

.dark {
  background: #111;
  border: 1.5px solid #fff;
  border-radius: 0 0 8px 8px;
}

.dark__icon {
  fill: #fff;
  stroke: #fff;
}

.dark-theme #toggle {
  background: #4f2ee8 linear-gradient(#4f2ee8 0%, #686868 100%);
  box-shadow: inset 1px 1px 2px rgba(0, 0, 0, .1);
}

.dark-theme #toggle:after {
  top: 1px;
  left: 21px;
}

.dark__icon, .dark__text {
  color: #fff;
}

.icon-black {
  stroke: #fff;
  fill: #fff;
}

.button-black {
  background-color: #111;
}

.k-modal {
  z-index: 100;
  letter-spacing: -.01em;
  text-transform: uppercase;
  color: #111;
  opacity: 0;
  pointer-events: none;
  visibility: hidden;
  background-color: #4f2ee8;
  justify-content: center;
  align-items: center;
  padding: 16px;
  font-family: inherit;
  font-size: 16px;
  font-weight: 700;
  line-height: 1.33;
  transition: all .3s;
  display: flex;
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  overflow: hidden;
}

@media screen and (min-width: 768px) {
  .k-modal {
    font-size: 18px;
  }
}

.k-modal--active {
  opacity: 1;
  pointer-events: initial;
  visibility: visible;
}

.k-modal, .k-modal *, .k-modal :before, .k-modal :after {
  box-sizing: border-box;
}

.k-modal:before {
  content: "";
  width: 1421px;
  height: 1421px;
  z-index: 0;
  filter: blur(539.791px);
  background: rgba(204, 236, 231, .5);
  position: absolute;
  top: -918px;
  left: -846px;
}

.k-modal__form {
  z-index: 1;
  max-width: 579px;
  width: 100%;
  background: #f6f6f6;
  border: 2px solid #111;
  border-radius: 18px;
  padding: 40px 20px;
  position: relative;
}

@media screen and (min-width: 768px) {
  .k-modal__form {
    padding: 80px 40px 40px;
  }
}

.k-modal__close {
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0);
  border: 0;
  padding: 0;
  display: flex;
  position: absolute;
  top: 14px;
  right: 14px;
}

@media screen and (max-width: 767px) {
  .k-modal__close {
    width: 20px;
    height: 20px;
  }
}

@media screen and (min-width: 768px) {
  .k-modal__close {
    top: 24px;
    right: 24px;
  }
}

.k-modal__footer {
  justify-content: center;
  align-items: center;
  gap: 20px;
  margin-top: 20px;
  display: flex;
}

.k-modal__footer button {
  font-family: inherit;
  font-size: inherit;
  font-weight: inherit;
  letter-spacing: inherit;
  color: rgba(17, 17, 17, .6);
  text-transform: uppercase;
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0);
  border: 0;
  padding: 0;
  transition: all .3s;
}

@media screen and (max-width: 767px) {
  .k-modal__footer button {
    font-size: 14px;
  }
}

.k-modal__footer button:focus, .k-modal__footer button:hover, .k-modal__footer button.active {
  color: #4f2ee8;
