  }
}

.k-modal__block input:focus + label, .k-modal__block input:hover + label {
  transform: translate(0, -100%);
}

.k-modal__block input.hide-label + label {
  opacity: 0;
}

.k-modal__block label {
  height: 100%;
  align-items: center;
  transition: all .5s;
  display: flex;
  position: absolute;
  top: 0;
  left: 24px;
}

@media screen and (min-width: 768px) {
